<template>
  <v-container
    fluid
    grid-list-md>
    <v-form
      ref="form2"
      @submit.prevent="validateBeforeSubmit">
      <core-progress-modal :show="loading" />
      <v-layout
        v-if="!loading"
        row
        wrap>
        <v-flex md12>
          <v-card class="elevation-0">
            <v-card-title>
              <h3 class="headline mb-0">Selecione as empresas para repasse</h3>
            </v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md3>
                  <core-label-value
                    :label="'Rede'"
                    :value="value.redeNome"
                  />
                </v-flex>
                <v-flex md3>
                  <core-label-value
                    :label="'Empresa'"
                    :value="value.empresaNome"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Vencimento até'"
                    :value="value.vencimentoAte | moment('DD/MM/YYYY')"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Pagamento em'"
                    :value="value.pagamentoEm | moment('DD/MM/YYYY')"
                  />
                </v-flex>
                <!--
                <v-flex md3>
                  <core-label-value
                    :label="'Comissão compras (% a.m.)'"
                    :value="value.peComissaoCompra"
                  />
                </v-flex>
                -->
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex md12>
          <v-card class="elevation-0">
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md2>
                  <core-label-value
                    :label="'Compras'"
                    :value="vlTotalSelecionado('vlTotalCompras')"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Comissão compras'"
                    :value="vlTotalSelecionado('vlTotalComissaoCompras')"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Créditos admin'"
                    :value="vlTotalSelecionado('vlTotalCreditoAdmin')"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Pagamentos loja'"
                    :value="vlTotalSelecionado('vlTotalPagamentosLoja')"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Créditos lojista'"
                    :value="vlTotalSelecionado('vlTotalCreditoLojista')"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Valor a pagar'"
                    :value="vlTotalSelecionado('vlTotalPagar')"
                    :class-value="'headline font-weight-black'"
                  />
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex
          class="layout row align-center"
          md12>
          <v-btn
            color="primary"
            @click="gerarArquivoTEDs"
          >
            Gerar Arquivo Para TEDs
          </v-btn>
          <span
            class="ml-1 body-2"
          >Gera um arquivo de transferência com as informações bancárias e valores das lojas selecionadas, esse arquivo deverá ser enviado ao banco para realização das TEDs.</span>
        </v-flex>
        <v-flex md12>
          <v-card>
            <v-data-table
              v-model="selected"
              :headers="headers"
              :items="listaLojaRepasse"
              :expand="false"
              class="elevation-1 arrow-pagintion"
              hide-actions
              select-all
              item-key="lojaId"
            >
              <template
                slot="headers"
                scope="props"
              >
                <tr>
                  <th style="width:5%">
                    &nbsp;
                  </th>
                  <th style="width:5%">
                    <v-checkbox
                      :input-value="props.all"
                      :indeterminate="props.indeterminate"
                      primary
                      hide-details
                      @click.native="toggleAll"
                    />
                  </th>
                  <th
                    v-for="header in props.headers"
                    :key="header.text"
                    :class="header.class"
                  >
                    {{ header.text }}
                  </th>
                </tr>
              </template>
              <template
                slot="items"
                scope="props"
                ma-5>
                <tr
                  :active="props.selected">
                  <td>
                    <v-icon @click="props.item.expanded = !props.item.expanded">
                      {{ props.item.expanded ? 'mdi-minus' : 'mdi-plus' }}
                    </v-icon>
                  </td>
                  <td>
                    <v-checkbox
                      :input-value="props.selected"
                      primary
                      hide-details
                      @click="props.selected = !props.selected"
                    />
                  </td>
                  <td><span class="body-2">{{ props.item.nomeEmpresa }}</span></td>
                  <td class="text-xs-right">{{ props.item.vlTotalCompras.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}</td>
                  <td
                    v-negative="props.item.vlTotalComissaoCompras"
                    class="text-xs-right"
                  >
                    {{ props.item.vlTotalComissaoCompras.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}
                  </td>
                  <td
                    v-negative="props.item.vlTotalCreditoAdmin"
                    class="text-xs-right">
                    {{ props.item.vlTotalCreditoAdmin.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}
                  </td>
                  <td
                    v-negative="props.item.vlTotalPagamentosLoja"
                    class="text-xs-right">
                    {{ props.item.vlTotalPagamentosLoja.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}
                  </td>
                  <td class="text-xs-right">{{ props.item.vlTotalCreditoLojista.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}</td>
                  <td
                    v-negative="props.item.vlTotalPagar"
                    class="text-xs-right">
                    {{ props.item.vlTotalPagar.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}
                  </td>
                </tr>
                <tr v-if="props.item.expanded">
                  <td colspan="10">
                    <v-data-table
                      :headers="headersItems"
                      :items="props.item.items"
                      hide-actions
                      class="elevation-1 arrow-pagintion"
                    >
                      <template
                        slot="items"
                        slot-scope="{ item }"
                        ma-5>
                        <tr>
                          <td class="text-xs-center">{{ item.vencimento | moment("DD/MM/YYYY") }}</td>
                          <td class="text-xs-center">{{ item.vlTotalCompras.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}</td>
                          <td
                            v-negative="item.vlTotalComissaoCompras"
                            class="text-xs-center"
                          >
                            {{ item.vlTotalComissaoCompras.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}
                          </td>
                          <td
                            v-negative="item.vlTotalCreditoAdmin"
                            class="text-xs-center">
                            {{ item.vlTotalCreditoAdmin.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}
                          </td>
                          <td
                            v-negative="item.vlTotalPagamentosLoja"
                            class="text-xs-center">
                            {{ item.vlTotalPagamentosLoja.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}
                          </td>
                          <td class="text-xs-center">{{ item.vlTotalCreditoLojista.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}</td>
                          <td
                            v-negative="item.vlTotalPagar"
                            class="text-xs-center">
                            {{ item.vlTotalPagar.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}
                          </td>
                        </tr>
                      </template>
                    </v-data-table>
                  </td>
                </tr>
              </template>
            </v-data-table>
          </v-card>
        </v-flex>
        <v-flex md12>
          <v-layout class="justify-end">
            <v-btn
              dark
              @click="goBack">Voltar</v-btn>

            <v-btn
              :disabled="disableBtn"
              :loading="loadingBtn"
              type="submit"
              color="primary">Continuar</v-btn>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-form>
  </v-container>
</template>

<script>
import { MONEY } from '../../../constants'
import { RepasseBusiness } from '../../../business'
export default {
  props: {
    value: {
      type: Object,
      required: true
    },
    // eslint-disable-next-line vue/require-default-prop
    setData: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    goBack: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    nextStep: {
      type: Function
    }
  },
  data () {
    return {
      selected: [],
      listaLojaRepasse: [],
      money: MONEY,
      loadingBtn: false,
      headers: [
        {
          sortable: false,
          text: 'Empresa',
          value: 'nomeEmpresa',
          class: 'grey lighten-1',
          align: 'left'
        },
        {
          sortable: false,
          text: 'Compras',
          value: 'compras',
          class: 'grey lighten-1',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Comissão compras',
          value: 'comissaoCompras',
          class: 'grey lighten-1',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Créditos admin',
          value: 'creditosAdmin',
          class: 'grey lighten-1',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Pagtos em loja',
          value: 'pagtosLoja',
          class: 'grey lighten-1',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Créditos lojista',
          value: 'creditosLojista',
          class: 'grey lighten-1',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Valor a pagar',
          value: 'valorPagar',
          class: 'grey lighten-1',
          align: 'center'
        }
      ],
      headersItems: [
        {
          sortable: false,
          text: 'Vencimento',
          value: 'dataVencimento',
          class: 'grey lighten-1',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Compras',
          value: 'compras',
          class: 'grey lighten-1',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Comissão compras',
          value: 'comissaoCompras',
          class: 'grey lighten-1',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Créditos admin',
          value: 'creditosAdmin',
          class: 'grey lighten-1',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Pagtos em loja',
          value: 'pagtosLoja',
          class: 'grey lighten-1',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Créditos lojista',
          value: 'creditosLojista',
          class: 'grey lighten-1',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Valor a pagar',
          value: 'valorPagar',
          class: 'grey lighten-1',
          align: 'center'
        }
      ],
      totalItems: null,
      loading: false
    }
  },
  computed: {
    disableBtn () {
      return this.selected.length === 0
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  },
  beforeMount () {
    this.loading = true
    RepasseBusiness.calculaRepasseGeral(this.value)
      .then(response => {
        this.listaLojaRepasse = response.data
        this.toggleAll()
      })
      .finally(() => {
        this.loading = false
      })
  },
  methods: {
    toggleAll () {
      if (this.selected.length) this.selected = []
      else this.selected = this.listaLojaRepasse.slice()
    },
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.loadingBtn = true
          this.value.lojasSelecionadas = this.selected.map(value => value.lojaId)
          this.nextStep()
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    vlTotalSelecionado (attr) {
      if (this.selected.length === 0) {
        this.value.extratoRepasse[attr] = '0,00'
      } else {
        this.value.extratoRepasse[attr] = this.selected
          .map(item => item[attr])
          .reduce((total, valor) => total + valor)
          .toLocaleString('pt-BR', { minimumFractionDigits: 2 })
      }
      return this.value.extratoRepasse[attr]
    },
    async gerarArquivoTEDs () {
      this.value.lojasSelecionadas = this.selected.map(value => value.lojaId)
      if (this.value.lojasSelecionadas && this.value.lojasSelecionadas.length > 0) {
        this.loading = true
        RepasseBusiness.gerarArquivoTEDs(this.value)
          .then((response) => {
            const url = window.URL.createObjectURL(new Blob([response.data]))
            const link = document.createElement('a')
            link.href = url
            link.setAttribute('download', 'Arquivo-TEDs-' + this.$moment().format('DD-MM-YYYY H:mm') + '.txt')
            document.body.appendChild(link)
            link.click()
            this.loading = false
          }).catch(erro => {
            this.loading = false
            this.$root.showErro(erro.data)
          })
      } else {
        this.$root.showAlerta('Selecione no mínimo uma loja')
      }
    }
  }
}
</script>
